.user-info {
  display: flex;
}
.user-info .ava {
  width: 100px;
  flex-shrink: 0;
}
.user-info .name {
  font-size: 18px;
  border-bottom: 1px dashed #DBE2DF;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.user-info .content {
  width: 100%;
}

.checkin-user .list-date {
  display: flex;
  align-items: end;
  justify-content: center;
  column-gap: 20px;
  row-gap: 20px;
  padding-bottom: 12px;
  margin-bottom: 12px;
  border-bottom: 1px dashed #DBE2DF;
}
.checkin-user .list-date li {
  text-align: center;
  color: #656867;
}
.checkin-user .list-date li div {
  margin-bottom: 6px;
}
.checkin-user .list-date li .icon-svg {
  width: 24px;
  height: 24px;
  color: #034C31;
}
.checkin-user .list-date li.off .icon-svg {
  color: #ABB0AE;
}
.checkin-user .ghim {
  border-radius: 8px;
  border: 1px solid #F9A826;
  background: #FEF6E9;
  padding: 16px;
  max-width: 400px;
  margin: 0 auto;
}

.slide-comment .close-comment {
  position: absolute;
  right: 15px;
  top: 10px;
  width: 28px;
  height: 28px;
  border-radius: 5px;
  background: #fff;
  display: none;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.slide-comment .close-comment svg {
  width: 26px;
  height: 26px;
}
@media (max-width: 991px) {
  .slide-comment {
    position: fixed;
    background: #fff;
    top: 0;
    bottom: 0;
    right: -100%;
    width: 100% !important;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    z-index: 1001;
  }
  .slide-comment .close-comment {
    display: flex;
  }
  .slide-comment.overflow-y {
    overflow-y: scroll;
  }
  .slide-comment.open {
    right: 0;
  }
}